import React, { Component } from 'react';
import Markdown  from 'react-markdown';




class JsPage extends Component {
    constructor(props) {
        super(props);
    }




    render() {

        return (
            <div>
                <Markdown source={"将应用拆解为多个微服务 \n\n 服务之间可以通过类似HTTP这样的同步协议通信，或者通过像AMQP这样的异步协议通信;这些协议能够被大多数开发者所理解，并且并不局限于某种编程语,这意味着任何个微服务，都可以用最适合的开发语言来实现。"}/>
                <div align="center"><img style={{maxHeight:"300px",height:"300px"}} src="data:image/png;base64,"></img></div>
                <Markdown source={"微服务的扩容 \n\n 面向单体系统，扩容针对的是整个系统，而面向微服务架构，扩容却只需要针对单个服务，这意味着你可以选择仅扩容那些需要更多资源的服务而保持其他的服\n" +
                "务仍然维持在原来的规模。"}/>
                <div align="center"><img style={{maxHeight:"300px",height:"300px"}} src="data:image/png;base64,"></img></div>
                <Markdown source={"用 Linux 容器技术隔离组件\n\n开发者不是使用虚拟机来隔离每个微服务环境（或者通常说的软件进程）， 而是正在转向 Linux 容器技术。 容器允许你在同一台机器上运行多个服务， 不仅提供不同的环境给每个服务， 而且将它们互相隔离。 容器类似虚拟机， 但开销小很多。\n\n一个容器里运行的进程实际上运行在宿主机的操作系统上， 就像所有其他进程一样（不像虚拟机， 进程是运行在不同的操作系统上的）。 但在容器里的进程仍然是和其他进程隔离的。 对于容器内进程本身而言， 就好像是在机器和操作系统上运行的唯一一个进程。比较虚拟机和容器\n\n" +
                "和虚拟机比较， 容器更加轻量级， 它允许在相同的硬件上运行更多数量的组件。主要是因为每个虚拟机需要运行自己的一组系统进程， 这就产生了除组件进程消耗以外的额外计算资源损耗。 从另 一方面说， 一个容器仅仅是运行在宿主机上被隔离的单个进程， 仅消耗应用容器消耗的资源， 不会有其他进程的开销。　\n\n" +
                "因为虚拟机的额外开销， 导致没有足够的资源给每个应用开一个专用的虚拟机，最终会将多个应用程序分组塞进每个虚拟机。 当使用容器时， 正如下图所示， 能够让每个应用有一个容器。 最终结果就是可以在同一台裸机上运行更多的应用程序。"}/>
                <div align="center"><img style={{maxHeight:"300px",height:"300px"}} src="data:image/jpg;base64,"></img></div>
                <Markdown source={"虚拟机的主要好处是它们提供完全隔离的环境， 因为每个虚拟机运行在它自己的Linux内核上， 而容器都是调用同一个内核， 这自然会有安全隐患。 每个虚拟机运行它自己的一组系统服务， 而容器则不会， 因为它们都运行在同一个操作系统上。 那也就意味着运行一个容器不用像虚拟机那样要开机， 它的进程可以很快被启动。\n\n容器实现隔离机制介绍\n\n" +
                "有两个机制可用：第一个是 Linux 命名空间， 它使每个进程只看到它自己的系统视图（文件、进程、网络接口、主机名等）；第二个是Linux 控制组 (cgroups),它限制了进程能使用的资源量 (CPU、 内存、 网络带宽等）。\n\n" +
                "用 Linux 命名空间隔离进程\n\n" +
                "默认情况下， 每个 Linux 系统最初仅有一个命名空间。 所有系统资源（诸如文件系统、 用户 ID、 网络接口等） 属于这一个命名空间。 但是你能创建额外的命名空间， 以及在它们之间组织资源。 对于一个进程， 可以在其中一个命名空间中运行它。进程将只能看到同一个命名空间下的资源。 当然， 会存在多种类型的多个命名空间，所以一个进程不单单只属于某一个命名空间， 而属于每个类型的一个命名空间。\n\n存在以下类型的命名空间：\n\n" +
                "Mount (mnt)\n\n" +
                "Process ID (pid)\n\n" +
                "Network (net)\n\n" +
                "Inter-process communicaion (ipd)\n\n" +
                "UTS\n\n" +
                "User ID (user)\n\n每种命名空间被用来隔离一组特定的资源。 例如， UTS 命名空间决定了运行在命名空间里的进程能看见哪些主机名和域名。 通过分派两个不同的 UTS 命名空间给一对进程， 能使它们看见不同的本地主机名。\n\n" +
                "Linux 控制组隔离进程\n\n" +
                "cgroups 是一个 Linux 内核功能， 它被用来限制一个进程或者一组进程的资源使用。一个进程的资源(CPU、 内存、 网络带宽等）使用量不能超出被分配的量。 这种方式下， 进程不能过分使用为其他进程保留的资源， 这和进程运行在不同的机器上是类似的。"}/>



            </div>
        );
    }
}

export default JsPage;


